  /* 
      Style for the background. 
      
      Generate your custom css gradients at https://cssgradient.io/ 
  */
  body {
      font-family: monospace;
      overflow: hidden;
  }
  .container {
      padding-top: 56.25%;
      position: relative;
      width: 100%;            
  }
  
  footer {
      font-size: 70%;
      color: white;
      position: absolute;
      bottom: 0.5%;
      left: 1%;
      opacity: 90%;
      text-shadow: black 1px 1px;
  }
  
  footer>a {
      color: white;
      text-decoration: none;
  }
  iframe {
    position: relative;
    width: 100%;
    height: 100%;
    border: 0;
    left: 0; top: 0;
  }
  
  /* 
      Style for the boxes.
  
      https://alligator.io/css/gradient-borders-pure-css/ 
  */
  .source {
      border: 0.5rem solid;
      border-radius: 0.5rem;
  
      transition: left 0.5s, right 0.5s, bottom 0.5s, top 0.5s, width 0.5s, height 0.5s, opacity 1s;
  }
  
  .hidden {
      display: none;
  }
  
  .rotate {
      transform: rotate(180deg);
      transform-origin: 50% 50%;
  }
  
  .ms-Icon {
      vertical-align: middle;
      margin-right: 0.2rem;
  }
  
  .item {
      cursor: pointer;
  }
  .error {
      border: solid 1px red;
      margin: 1rem 0rem 1rem 0rem;
      padding: 1rem;
      background: white;
  }
  #toolbox > button {
      height: 2rem;
      width: 2rem;
      border-radius: 1rem;
      background: black;
      color: white;
      padding: 0.47rem;
      margin: 0.25rem;
      cursor: pointer;
      opacity: 40%;
  }
  #toolbox > button:hover {
      opacity: 100%;
  }
  #toolbox > button.active {
      background: white;
      color: black;
  }
  #banner {
      margin: 0.5vh;
  }
  canvas {
      display: none;
  }
  .paint canvas {
      display: block;
      position:absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
  }
  .paint {
      cursor:pointer !important;
  }
  
  body.loading {
      filter: blur(0.2vw);
  }
  
  /*
  Settings
  */
  #settings {
      position: absolute;
      left: 16%;
      top: 16%;
      width: 58%;
      height: 58%;
      background: #ccc;
      padding: 5% 5% 5% 5%;
      overflow-y: auto;
  }
  
  #settings .field {
      padding: 1% 0 1% 0;
  }
  
  #settings .field input {
      width: 100%;
      font-size: 90%;
  }
  
  #settings .field input[type="checkbox"] {
      width: unset;
  }
  
  #settings .field select {
      width: 100%;
      font-size: 90%;
  }
  
  #settings #settingsclose {
      position: absolute;
      right: 2%;
      top: 2%;
  }
  
  /*
      MakeCode editor box
  */
  #editor {
      position: absolute;
      width: 94%;
      height: 87%;
      top: 1%;
      transition-delay: 0.2s;
  }

  iframe {
    position: relative;
    width: 100%;
    height: 100%;
    border: 0;
    left: 0; top: 0;
  }
  
  .multi #editor {
      width: 50%;
  }
  
  #editor2 {
      position: absolute;
      width: 50%;
      height: 87%;
      top: 1%;
      transition-delay: 0.3s;
  }
  
  /*
      Face camera            
  */
  #facecam, #hardwarecam {
      position: absolute;
      width: 30%;
      bottom: 1.5%;
      right: 1%;
  }
  #hardwarecam {
      width: 0;
      right: -1rem;
      opacity: 0;
  }
  .hardware #hardwarecam {
      opacity: 100%;
  }
  
  /*
      Chat window
  */
  #chat {
      position: absolute;
      width: 0;
      height: 60%;
      top: 1%;
      right: 1%;
      display: none;
      transition-delay: 0.3s;
  }
  
  #social {
      position: absolute;
      width: 60%;
      height: 5%;
      left: 1%;
      bottom: 2%;
      background: #615fc7;
      transition-delay: 0.1s;
  }
  #toolbox {
      float:right;
  }
  
  span.social {
      font-size: 2vh;
      color: white;
      flex-grow: 1;
      text-align: center;
      cursor: pointer;
  }
  video {
      position: relative;
      width: 100%;
  }
  video.error {
      background: rgba(255, 0, 0, 0.5)
  }
  
  /** 
  * chat visibility
  */
  .chat #chat {
      display: block;
      opacity: 90%;
  }
  
  /* rightscene: browser with facecam on right */
  .rightscene #editor {
      left: 1%;
  }
  
  .rightscene #facecam {
      right: 1%;
      width: 28%;
  }
  
  .rightscene #chat {
      right: 1%;
      width: 28%;
      height: 54%;
  }
  
  .rightscene.chat #editor {
      width: 67%;
      top: 1%;
      height: 87%;
  }
  
  .rightscene.hardware #hardwarecam {
      right: 1%;
      width: 22%;
      bottom: 42%;
  }
  
  .rightscene.multi #editor {
      width: 67%;
  }
  
  .rightscene.multi #editor2 {
      width: 28%;
      left: 70%;
      height: 54%;
  }
  .rightscene.multi.hardware #hardwarecam {
      right: 1%;
      width: 22%;
      bottom: 24%;
  }
  
  /* leftscene: browser with facecam on left left */
  .leftscene #editor {
      left: 4%;
      height: 87%;
  }
  
  .leftscene #facecam {
      right: 70%;
      width: 28%;
  }
  
  .leftscene #hardwarecam {
      bottom: 9%;
      right: 1%;
  }
  
  .leftscene #social {
      width: 55%;
      height: 5%;
      left: 43%;
      bottom: 1.5%;
  }
  .leftscene.hardware #hardwarecam {
      width: 22%;
  }
  
  /* left+multi*/
  .leftscene.multi #editor {
      left: 1%;
      width: 28%;
      height: 54%;
  }
  
  .leftscene.multi #editor2 {
      width: 67%;
      left: 31%;
  }
  
  .leftscene.multi #facecam {
      right: 70%;
      width: 28%;
  }
  .leftscene.multi.hardware #hardwarecam {
      right: 76%;
      width: 22%;
      bottom: 24%;
  }
  
  /** leftscene + chat **/
  .leftscene.chat #editor {
      left: 31%;
      width: 67%;
  }
  
  .leftscene.chat #facecam {
      right: 70%;
      width: 28%;
  }
  
  .leftscene.chat #chat {
      right: 70%;
      height: 54%;
      width: 28%;
  }
  
  /* chatscene: facecam and stream chat */
  .chatscene #facecam {
      bottom: 19%;
      right: 38%;
      width: 52%;
  }
  
  .chatscene #editor {
      left: -150rem;
  }
  .chatscene #editor2 {
      right: -150rem;
  }
  
  .chatscene #chat {
      top: 9%;
      right: 4%;
      width: 24%;
      height: 80%;
      opacity: 100%;
  }
  
  .chatscene #social {
      left: 4%;
      bottom: 9%;
  }
  
  .chatscene.hardware #facecam {
      bottom: 19%;
      right: 49%;
      width: 46%;
  }
  .chatscene.hardware #hardwarecam {
      width: 46%;
      bottom: 19%;
      right: 1%;            
  }
  